
<template>
	<div class="edit_view" :style='{}'>
		<div class="bread_view">
			<el-breadcrumb separator="/" class="breadcrumb">
				<el-breadcrumb-item class="first_breadcrumb" :to="{ path: '/' }"></el-breadcrumb-item>
				<el-breadcrumb-item class="second_breadcrumb" v-for="(item,index) in breadList" :key="index">{{item.name}}</el-breadcrumb-item>
			</el-breadcrumb>
		</div>
		<el-form ref="formRef" :model="form" class="add_form" label-width="120px" :rules="rules">
			<el-row>
				<el-col :span="24">
					<el-form-item label="" prop="shebeimingcheng">
						<el-input class="list_inp" v-model="form.shebeimingcheng" placeholder=""
							 type="text" 							:readonly="!isAdd||disabledForm.shebeimingcheng?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="24">
					<el-form-item label="" prop="tupian">
						<uploads
							:disabled="!isAdd||disabledForm.tupian?true:false"
							action="file/upload" 
							tip="" 
							:limit="3" 
							style="width: 100%;text-align: left;"
							:fileUrls="form.tupian?form.tupian:''" 
							@change="tupianUploadSuccess">
						</uploads>
					</el-form-item>
				</el-col>
				<el-col :span="24">
					<el-form-item label="" prop="shebeileixing">
						<el-select
							class="list_sel"
							:disabled="!isAdd||disabledForm.shebeileixing?true:false"
							v-model="form.shebeileixing" 
							placeholder=""
							style="width:100%;"
							>
							<el-option v-for="(item,index) in shebeileixingLists" :label="item"
								:value="item"
								>
							</el-option>
						</el-select>
					</el-form-item>
				</el-col>
				<el-col :span="24">
					<el-form-item label="" prop="shebeixinghao">
						<el-input class="list_inp" v-model="form.shebeixinghao" placeholder=""
							 type="text" 							:readonly="!isAdd||disabledForm.shebeixinghao?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="24">
					<el-form-item label="" prop="shebeiweizhi">
						<el-input class="list_inp" v-model="form.shebeiweizhi" placeholder=""
							 type="text" 							:readonly="!isAdd||disabledForm.shebeiweizhi?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="24">
					<el-form-item label="" prop="gongneng">
						<el-input class="list_inp" v-model="form.gongneng" placeholder=""
							 type="text" 							:readonly="!isAdd||disabledForm.gongneng?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="24">
					<el-form-item label="" prop="yonghu">
						<el-input class="list_inp" v-model="form.yonghu" placeholder=""
							 type="text" 							:readonly="!isAdd||disabledForm.yonghu?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="24">
					<el-form-item label="" prop="xingming">
						<el-input class="list_inp" v-model="form.xingming" placeholder=""
							 type="text" 							:readonly="!isAdd||disabledForm.xingming?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="24">
					<el-form-item label="" prop="gonghao">
						<el-select
							class="list_sel"
							:disabled="!isAdd||disabledForm.gonghao?true:false"
							v-model="form.gonghao" 
							placeholder=""
							style="width:100%;"
							@change="gonghaoChange">
							<el-option v-for="(item,index) in gonghaoLists" :label="item" :value="item">
							</el-option>
						</el-select>
					</el-form-item>
				</el-col>
				<el-col :span="24">
					<el-form-item label="" prop="weixiuren">
						<el-input class="list_inp" v-model="form.weixiuren" placeholder=""
							 type="text" 							:readonly="!isAdd||disabledForm.weixiuren?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="24">
					<el-form-item label="" prop="weixiujindu">
						<el-select
							class="list_sel"
							:disabled="!isAdd||disabledForm.weixiujindu?true:false"
							v-model="form.weixiujindu" 
							placeholder=""
							style="width:100%;"
							>
							<el-option v-for="(item,index) in weixiujinduLists" :label="item"
								:value="item"
								>
							</el-option>
						</el-select>
					</el-form-item>
				</el-col>
				<el-col :span="24">
					<el-form-item label="" prop="weixiujine">
						<el-input class="list_inp" v-model.number="form.weixiujine" placeholder=""
							 type="text" 							:readonly="!isAdd||disabledForm.weixiujine?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="24">
					<el-form-item label="" prop="guzhangxinxi">
						<el-input v-model="form.guzhangxinxi" placeholder="" type="textarea"
						:readonly="!isAdd||disabledForm.guzhangxinxi?true:false"
						/>
					</el-form-item>
				</el-col>
				<el-col :span="24">
					<el-form-item label="" prop="shebeixiangqing">
						<el-input v-model="form.shebeixiangqing" placeholder="" type="textarea"
						:readonly="!isAdd||disabledForm.shebeixiangqing?true:false"
						/>
					</el-form-item>
				</el-col>
			</el-row>
			<div class="formModel_btn_box">
				<el-button class="formModel_cancel" @click="backClick"></el-button>
				<el-button class="formModel_confirm"
                           @click="save"
                           type="success"
				>
					
				</el-button>
			</div>
		</el-form>
	</div>
</template>
<script setup>
	import {
		ref,
		getCurrentInstance,
		watch,
		onUnmounted,
		onMounted,
		nextTick,
		computed
	} from 'vue';
	import {
		useRoute,
		useRouter
	} from 'vue-router';
	const context = getCurrentInstance()?.appContext.config.globalProperties;
	const route = useRoute()
	const router = useRouter()
	//
	const tableName = 'shebeiweixiu'
	const formName = ''
	//
	const breadList = ref([{
		name: formName
	}])
	//
	const getUUID =()=> {
      return new Date().getTime();
    }
	//form
	const form = ref({
		shebeimingcheng: '',
		tupian: '',
		shebeileixing: '',
		shebeixinghao: '',
		shebeiweizhi: '',
		guzhangxinxi: '',
		gongneng: '',
		shebeixiangqing: '',
		yonghu: '',
		xingming: '',
		gonghao: '',
		weixiuren: '',
		weixiujindu: '',
		weixiujine: 0,
	})
	const formRef = ref(null)
	const id = ref(0)
	const type = ref('')
	const disabledForm = ref({
		shebeimingcheng : false,
		tupian : false,
		shebeileixing : false,
		shebeixinghao : false,
		shebeiweizhi : false,
		guzhangxinxi : false,
		gongneng : false,
		shebeixiangqing : false,
		yonghu : false,
		xingming : false,
		gonghao : false,
		weixiuren : false,
		weixiujindu : false,
		weixiujine : false,
	})
	const isAdd = ref(false)
	//
	//
	const validateIntNumber = (rule, value, callback) => {
		if (!value) {
			callback();
		} else if (!context?.$toolUtil.isIntNumer(value)) {
			callback(new Error(""));
		} else {
			callback();
		}
	}
	//
	const validateNumber = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isNumber(value)) {
			callback(new Error(""));
		} else {
			callback();
		}
	}
	//
	const validateMobile = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isMobile(value)) {
			callback(new Error(""));
		} else {
			callback();
		}
	}
	//
	const validatePhone = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isPhone(value)) {
			callback(new Error(""));
		} else {
			callback();
		}
	}
	//
	const validateEmail = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isEmail(value)) {
			callback(new Error(""));
		} else {
			callback();
		}
	}
	//
	const validateIdCard = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.checkIdCard(value)) {
			callback(new Error(""));
		} else {
			callback();
		}
	}
	//
	const validateUrl = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isURL(value)) {
			callback(new Error("URL"));
		} else {
			callback();
		}
	}
	const rules = ref({
		shebeimingcheng: [
			{required: true,message: '',trigger: 'blur'}, 
		],
		tupian: [
		],
		shebeileixing: [
		],
		shebeixinghao: [
			{required: true,message: '',trigger: 'blur'}, 
		],
		shebeiweizhi: [
			{required: true,message: '',trigger: 'blur'}, 
		],
		guzhangxinxi: [
		],
		gongneng: [
		],
		shebeixiangqing: [
		],
		yonghu: [
		],
		xingming: [
		],
		gonghao: [
		],
		weixiuren: [
		],
		weixiujindu: [
		],
		weixiujine: [
			{ validator: validateIntNumber, trigger: 'blur' },
		],
	})
	//
	const tupianUploadSuccess=(e)=>{
		form.value.tupian = e
	}
	//
	const shebeileixingLists = ref([])
	//
	const gonghaoLists = ref([])
	//
	const weixiujinduLists = ref([])
	//methods

	//methods
	//info
	const getInfo = ()=>{
		context?.$http({
			url: `${tableName}/info/${id.value}`,
			method: 'get'
		}).then(res => {
			let reg=new RegExp('../../../file','g')
			form.value = res.data.data
		})
	}
	const crossRow = ref('')
	const crossTable = ref('')
	const crossTips = ref('')
	const crossColumnName = ref('')
	const crossColumnValue = ref('')
	//
	const init = (formId=null,formType='add',formNames='',row=null,table=null,statusColumnName=null,tips=null,statusColumnValue=null) => {
		if(formId){
			id.value = formId
			type.value = formType
		}
		if(formType == 'add'){
			isAdd.value = true
		}else if(formType == 'info'){
			isAdd.value = false
			getInfo()
		}else if(formType == 'edit'){
			isAdd.value = true
			getInfo()
		}
		else if(formType == 'cross'){
			isAdd.value = true
			// getInfo()
			for(let x in row){
				if(x=='shebeimingcheng'){
					form.value.shebeimingcheng = row[x];
					disabledForm.value.shebeimingcheng = true;
					continue;
				}
				if(x=='tupian'){
					form.value.tupian = row[x];
					disabledForm.value.tupian = true;
					continue;
				}
				if(x=='shebeileixing'){
					form.value.shebeileixing = row[x];
					disabledForm.value.shebeileixing = true;
					continue;
				}
				if(x=='shebeixinghao'){
					form.value.shebeixinghao = row[x];
					disabledForm.value.shebeixinghao = true;
					continue;
				}
				if(x=='shebeiweizhi'){
					form.value.shebeiweizhi = row[x];
					disabledForm.value.shebeiweizhi = true;
					continue;
				}
				if(x=='guzhangxinxi'){
					form.value.guzhangxinxi = row[x];
					disabledForm.value.guzhangxinxi = true;
					continue;
				}
				if(x=='gongneng'){
					form.value.gongneng = row[x];
					disabledForm.value.gongneng = true;
					continue;
				}
				if(x=='shebeixiangqing'){
					form.value.shebeixiangqing = row[x];
					disabledForm.value.shebeixiangqing = true;
					continue;
				}
				if(x=='yonghu'){
					form.value.yonghu = row[x];
					disabledForm.value.yonghu = true;
					continue;
				}
				if(x=='xingming'){
					form.value.xingming = row[x];
					disabledForm.value.xingming = true;
					continue;
				}
				if(x=='gonghao'){
					form.value.gonghao = row[x];
					disabledForm.value.gonghao = true;
					continue;
				}
				if(x=='weixiuren'){
					form.value.weixiuren = row[x];
					disabledForm.value.weixiuren = true;
					continue;
				}
				if(x=='weixiujindu'){
					form.value.weixiujindu = row[x];
					disabledForm.value.weixiujindu = true;
					continue;
				}
				if(x=='weixiujine'){
					form.value.weixiujine = row[x];
					disabledForm.value.weixiujine = true;
					continue;
				}
			}
			if(row){
				crossRow.value = row
			}
			if(table){
				crossTable.value = table
			}
			if(tips){
				crossTips.value = tips
			}
			if(statusColumnName){
				crossColumnName.value = statusColumnName
			}
			if(statusColumnValue){
				crossColumnValue.value = statusColumnValue
			}
		}
		context?.$http({
			url: `${context?.$toolUtil.storageGet('frontSessionTable')}/session`,
			method: 'get'
		}).then(res => {
			var json = res.data.data
			if(json.hasOwnProperty('yonghu') && context?.$toolUtil.storageGet("frontRole")!=""){
				form.value.yonghu = json.yonghu
				disabledForm.value.yonghu = true;
			}
			if(json.hasOwnProperty('xingming') && context?.$toolUtil.storageGet("frontRole")!=""){
				form.value.xingming = json.xingming
				disabledForm.value.xingming = true;
			}
            if (localStorage.getItem('autoSave')) {
                localStorage.removeItem('autoSave')
                save()
            }
		})
		context?.$http({
			url: `option/shebeileixing/shebeileixing`,
			method: 'get'
		}).then(res=>{
			shebeileixingLists.value = res.data.data
		})
		context?.$http({
			url: `option/weixiugong/gonghao`,
			method: 'get'
		}).then(res=>{
			gonghaoLists.value = res.data.data
		})
		//
		disabledForm.value.weixiuren = true;
		weixiujinduLists.value = ",".split(',')
	}
	//
	//
	const backClick = () => {
		history.back()
	}
	//
	const gonghaoChange=()=>{
		context?.$http({
			url: `follow/weixiugong/gonghao?columnValue=` + form.value.gonghao,
			method: 'get'
		}).then(res=>{
			//
			if(res.data.data.weixiuren){
				form.value.weixiuren = res.data.data.weixiuren
			}
		})
	}
	//
	const save=()=>{
		if(form.value.tupian!=null) {
			form.value.tupian = form.value.tupian.replace(new RegExp(context?.$config.url,"g"),"");
		}
		var table = crossTable.value
		var objcross = JSON.parse(JSON.stringify(crossRow.value))
		let crossUserId = ''
		let crossRefId = ''
		let crossOptNum = ''
		if(type.value == 'cross'){
			if(crossColumnName.value!=''){
				if(!crossColumnName.value.startsWith('[')){
					for(let o in objcross){
						if(o == crossColumnName.value){
							objcross[o] = crossColumnValue.value
						}
					}
					//
					changeCrossData(objcross)
				}else{
					crossUserId = context?.$toolUtil.storageGet('userid')
					crossRefId = objcross['id']
					crossOptNum = crossColumnName.value.replace(/\[/,"").replace(/\]/,"")
				}
			}
		}
		formRef.value.validate((valid)=>{
			if(valid){
				if(crossUserId&&crossRefId){
					form.value.crossuserid = crossUserId
					form.value.crossrefid = crossRefId
					let params = {
						page: 1,
						limit: 1000, 
						crossuserid:form.value.crossuserid,
						crossrefid:form.value.crossrefid,
					}
					context?.$http({
						url: `${tableName}/page`,
						method: 'get', 
						params: params 
					}).then(res=>{
						if(res.data.data.total>=crossOptNum){
							context?.$toolUtil.message(`${crossTips.value}`,'error')
							return false
						}else{
							context?.$http({
								url: `${tableName}/${!form.value.id ? "save" : "update"}`,
								method: 'post', 
								data: form.value 
							}).then(res=>{
								context?.$toolUtil.message(``,'success')
                                history.back()
							})
						}
					})
				}else{
					context?.$http({
						url: `${tableName}/${!form.value.id ? "save" : "update"}`,
						method: 'post', 
						data: form.value 
					}).then(res=>{
                        context?.$toolUtil.message(``,'success')
                        history.back()
					})
				}
			}
		})
	}
	//
	const changeCrossData=(row)=>{
		context?.$http({
			url: `${crossTable.value}/update`,
			method: 'post',
			data: row
		}).then(res=>{})
	}
	onMounted(()=>{
		type.value = route.query.type?route.query.type:'add'
		let row = null
		let table = null
		let statusColumnName = null
		let tips = null
		let statusColumnValue = null
		if(type.value == 'cross'){
			row = context?.$toolUtil.storageGet('crossObj')?JSON.parse(context?.$toolUtil.storageGet('crossObj')):{}
			table = context?.$toolUtil.storageGet('crossTable')
			statusColumnName = context?.$toolUtil.storageGet('crossStatusColumnName')
			tips = context?.$toolUtil.storageGet('crossTips')
			statusColumnValue = context?.$toolUtil.storageGet('crossStatusColumnValue')
		}
		init(route.query.id?route.query.id:null, type.value,'', row, table, statusColumnName, tips, statusColumnValue)
	})
    onUnmounted(()=>{
        Object.keys(localStorage).map(item=>{
            if(item.startsWith('cross')){
                localStorage.removeItem(item)
            }
        })
    })
</script>
<style lang="scss" scoped>
	// 
	.bread_view {
		:deep(.breadcrumb) {
			.el-breadcrumb__separator {
			}
			.first_breadcrumb {
				.el-breadcrumb__inner {
				}
			}
			.second_breadcrumb {
				.el-breadcrumb__inner {
				}
			}
		}
	}
	// 
	.add_form{
		// form item
		:deep(.el-form-item) {
			//label
			.el-form-item__label {
			}
			// 
			.el-form-item__content {
				// 
				.list_inp {

				}
				// 
				.list_sel {
					//
					.select-trigger{
						height: 100%;
						.el-input{
							height: 100%;
						}
					}
				}
				// 
				.el-textarea__inner {
				}
				//
				.el-upload-list  {
					//
					.el-upload__tip {
					}
					//
					.el-upload--picture-card {
						//
						.el-icon{
						}
					}
					.el-upload-list__item {
					}
				}
			}
		}
	}
	// 
	.formModel_btn_box {
		.formModel_cancel {
		}
		.formModel_cancel:hover {
		}
		
		.formModel_confirm {
		}
		.formModel_confirm:hover {
		}
	}
</style>
<style>
.edit_view {
    width: 1200px;
    margin: 20px auto;
    padding: 0px ;
    background: #fff;
    overflow: hidden;
    font-size: 16px;
    color:#666;
}
.edit_view .add_form{
    width: 100%;
    padding: 30px;
    border:0px solid #eee
}
.edit_view .add_form .el-form-item{
    margin: 0px 0px 20px;
    display: flex;
}
.edit_view .add_form .el-form-item .el-form-item__label{
    width: 150px;
    background: none;
    text-align: right;
    display: block;
    font-size: 16px;
    color: rgb(51, 51, 51);
    font-weight: 500;
}
.edit_view .add_form .el-form-item .el-form-item__content{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;
    width: calc(100% - 120px);
}
.edit_view .add_form .el-form-item .el-form-item__content .list_inp{
    height: 36px;
    line-height: 36px;
    border: 1px solid rgb(221, 221, 221);
    padding: 0px 10px;
    width: 100%;
    box-sizing: border-box;
    background: rgb(255, 255, 255);
    font-size: 16px;
}

.edit_view .add_form .el-form-item .el-form-item__content .list_sel{
    line-height: 36px;
    border: 1px solid rgb(221, 221, 221);
    box-sizing: border-box;
    width: calc(100% - 0px);
    padding: 0px 10px;
    border-radius: 0px;
    background: rgb(255, 255, 255);
    font-size: 16px;
}





.edit_view .add_form .el-form-item .el-form-item__content .el-textarea{
}
.edit_view .add_form .el-form-item .el-form-item__content .el-textarea .el-textarea__inner{
    width: 100%;
    min-height: 150px;
    padding: 12px;
    border: 1px solid rgb(221, 221, 221);
    border-radius: 0px;
    color: #666;
