import { createRouter, createWebHashHistory } from 'vue-router'
import index from '../views'
import home from '../views/pages/home.vue'
import login from '../views/pages/login.vue'
import newsList from '@/views/pages/news/list'
import aboutusList from '@/views/pages/aboutus/list'
import aboutusDetail from '@/views/pages/aboutus/formModel'
import aboutusAdd from '@/views/pages/aboutus/formAdd'
import systemintroList from '@/views/pages/systemintro/list'
import systemintroDetail from '@/views/pages/systemintro/formModel'
import systemintroAdd from '@/views/pages/systemintro/formAdd'
import yonghuList from '@/views/pages/yonghu/list'
import yonghuDetail from '@/views/pages/yonghu/formModel'
import yonghuAdd from '@/views/pages/yonghu/formAdd'
import yonghuRegister from '@/views/pages/yonghu/register'
import yonghuCenter from '@/views/pages/yonghu/center'
import weixiugongList from '@/views/pages/weixiugong/list'
import weixiugongDetail from '@/views/pages/weixiugong/formModel'
import weixiugongAdd from '@/views/pages/weixiugong/formAdd'
import shebeileixingList from '@/views/pages/shebeileixing/list'
import shebeileixingDetail from '@/views/pages/shebeileixing/formModel'
import shebeileixingAdd from '@/views/pages/shebeileixing/formAdd'
import shebeixinxiList from '@/views/pages/shebeixinxi/list'
import shebeixinxiDetail from '@/views/pages/shebeixinxi/formModel'
import shebeixinxiAdd from '@/views/pages/shebeixinxi/formAdd'
import yuyueweixiuList from '@/views/pages/yuyueweixiu/list'
import yuyueweixiuDetail from '@/views/pages/yuyueweixiu/formModel'
import yuyueweixiuAdd from '@/views/pages/yuyueweixiu/formAdd'
import shebeiweixiuList from '@/views/pages/shebeiweixiu/list'
import shebeiweixiuDetail from '@/views/pages/shebeiweixiu/formModel'
import shebeiweixiuAdd from '@/views/pages/shebeiweixiu/formAdd'
import wodedingdanList from '@/views/pages/wodedingdan/list'
import wodedingdanDetail from '@/views/pages/wodedingdan/formModel'
import wodedingdanAdd from '@/views/pages/wodedingdan/formAdd'
import fuwupingjiaList from '@/views/pages/fuwupingjia/list'
import fuwupingjiaDetail from '@/views/pages/fuwupingjia/formModel'
import fuwupingjiaAdd from '@/views/pages/fuwupingjia/formAdd'
import liuyanfankuiList from '@/views/pages/liuyanfankui/list'
import liuyanfankuiDetail from '@/views/pages/liuyanfankui/formModel'
import liuyanfankuiAdd from '@/views/pages/liuyanfankui/formAdd'
import gongzijixiaoList from '@/views/pages/gongzijixiao/list'
import gongzijixiaoDetail from '@/views/pages/gongzijixiao/formModel'
import gongzijixiaoAdd from '@/views/pages/gongzijixiao/formAdd'

const routes = [{
		path: '/',
		redirect: '/index/home'
	},
	{
		path: '/index',
		component: index,
		children: [{
			path: 'home',
			component: home
		}
		, {
			path: 'newsList',
			component: newsList
		}
		, {
			path: 'aboutusList',
			component: aboutusList
		}, {
			path: 'aboutusDetail',
			component: aboutusDetail
		}, {
			path: 'aboutusAdd',
			component: aboutusAdd
		}
		, {
			path: 'systemintroList',
			component: systemintroList
		}, {
			path: 'systemintroDetail',
			component: systemintroDetail
		}, {
			path: 'systemintroAdd',
			component: systemintroAdd
		}
		, {
			path: 'yonghuList',
			component: yonghuList
		}, {
			path: 'yonghuDetail',
			component: yonghuDetail
		}, {
			path: 'yonghuAdd',
			component: yonghuAdd
		}
		, {
			path: 'yonghuCenter',
			component: yonghuCenter
		}
		, {
			path: 'weixiugongList',
			component: weixiugongList
		}, {
			path: 'weixiugongDetail',
			component: weixiugongDetail
		}, {
			path: 'weixiugongAdd',
			component: weixiugongAdd
		}
		, {
			path: 'shebeileixingList',
			component: shebeileixingList
		}, {
			path: 'shebeileixingDetail',
			component: shebeileixingDetail
		}, {
			path: 'shebeileixingAdd',
			component: shebeileixingAdd
		}
		, {
			path: 'shebeixinxiList',
			component: shebeixinxiList
		}, {
			path: 'shebeixinxiDetail',
			component: shebeixinxiDetail
		}, {
			path: 'shebeixinxiAdd',
			component: shebeixinxiAdd
		}
		, {
			path: 'yuyueweixiuList',
			component: yuyueweixiuList
		}, {
			path: 'yuyueweixiuDetail',
			component: yuyueweixiuDetail
		}, {
			path: 'yuyueweixiuAdd',
			component: yuyueweixiuAdd
		}
		, {
			path: 'shebeiweixiuList',
			component: shebeiweixiuList
		}, {
			path: 'shebeiweixiuDetail',
			component: shebeiweixiuDetail
		}, {
			path: 'shebeiweixiuAdd',
			component: shebeiweixiuAdd
		}
		, {
			path: 'wodedingdanList',
			component: wodedingdanList
		}, {
			path: 'wodedingdanDetail',
			component: wodedingdanDetail
		}, {
			path: 'wodedingdanAdd',
			component: wodedingdanAdd
		}
		, {
			path: 'fuwupingjiaList',
			component: fuwupingjiaList
		}, {
			path: 'fuwupingjiaDetail',
			component: fuwupingjiaDetail
		}, {
			path: 'fuwupingjiaAdd',
			component: fuwupingjiaAdd
		}
		, {
			path: 'liuyanfankuiList',
			component: liuyanfankuiList
		}, {
			path: 'liuyanfankuiDetail',
			component: liuyanfankuiDetail
		}, {
			path: 'liuyanfankuiAdd',
			component: liuyanfankuiAdd
		}
		, {
			path: 'gongzijixiaoList',
			component: gongzijixiaoList
		}, {
			path: 'gongzijixiaoDetail',
			component: gongzijixiaoDetail
		}, {
			path: 'gongzijixiaoAdd',
			component: gongzijixiaoAdd
		}
		]
	},
	{
		path: '/login',
		component: login
	}
	,{
		path: '/yonghuRegister',
		component: yonghuRegister
	}
]

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes
})

export default router
